/*                                                            General styles                                         */




@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap');

body{
  margin:0px;
  padding: 0px;
  background-color: #e6eff3;
  font-family: 'Lato', sans-serif;
}

*{
  box-sizing: border-box;
}


ul{
  list-style: none;
}
a{
  text-decoration: none;
}
button{
  outline: none;
  border: none;
}
input{
  outline: none;
  border: none;
}




/*                                                                Navbar styles                          */



.logo img{
  width: 90px;
  border-radius: 20px;
  margin-top: 20px;
}


nav{
	display: flex;
	justify-content: space-around;
	align-items: center;
	position: fixed;
	left: 0;
	top: 0;
	width:100%;
	z-index: 1;
    background-color: transparent;
    transition: 0.6s ease-in-out;
}
nav.active{
	box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.157);
	background-color: #000000;
}
nav ul{
	display: flex;
}
.active{
    background-color: #FF1414;
    color:#ffffff;
	box-shadow: 5px 10px 30px rgba(198, 64, 64, 0.411);
	transition: all ease 0.2s;
}
.active:hover{
    background-color: #000000;
    color:#ffffff;
	box-shadow: 5px 10px 30px rgba(198, 64, 64, 0.411);
	transition: all ease 0.2s;
}
nav ul li a{
    font-family: 'Lato';
	height:40px;
	line-height: 43px;
	margin: 3px;
	padding: 0px 22px;
	display: flex;
	font-size: 0.9rem;
	text-transform: uppercase;
	font-weight: 500;
	color:#ffffff;
    letter-spacing: 1px;
    border-radius: 3px;
    transition: 0.2s ease-in-out;
}
nav ul li a:hover{
	background-color: #04a6e6;
  color:#fdfdfd;
	box-shadow: 5px 10px 30px rgba(255, 2, 2, 0.336);
	transition: all ease 0.2s;
}
nav .menu-btn,
.menu-icon{
		display: none;
	}


/*                                                      Header styles                                         */
                      


#main {
    width: 100%;
    height: 768px;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-image: url('./edn images/airplane view 3.jpg');
    z-index: 0;
}

#main::before{
    position: absolute;
    content: "";

    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}

.name{
    text-align: center;
	font-family: 'Montserrat';
	width: 900px;
	position: absolute;
    left: 50%;
	top: 55%;
	transform: translate(-50%,-55%);
}
.name span{
    color: #FF1414;
}
.name .details{
    font-size: 23px;
    color: #c5c5c5;
}
.name h2{
	font-family:'Lato';
	font-size: 2.7rem;
	margin:0px;
	letter-spacing: 2px;
	color:#ffffff;
}
.name h1{
	font-family:'Lato';
	font-size: 5rem;
	margin:0px;
	letter-spacing: 2px;
	color:#ffffff;
}
.header-btns{
    display: flex;
    margin: 0px 0px 0px 0px;
}
.header-btn {
    width: auto; /* Allow the button to adjust its width based on content */
    display: inline-block; /* Display the button inline */
    padding: 10px 10px; /* Adjust padding as needed */
    margin: 0px 0px 0px 0px; /* Adjust margin as needed */
    font-size: 1.4rem;
    background-color: #FF1414;
    box-shadow: 5px 10px 30px rgba(255, 2, 2, 0.336);
    border-radius: 5px;
    color: #ffffff;
    text-decoration: none; /* Remove underline from link */
    border: none; /* Remove border */
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s; /* Smooth transition for hover effect */
  }
  
  .header-btn:hover {
    background-color: transparent;
    color: #ffffff;
    border: 2px solid #FF1414; /* Add border on hover */
  }
  

.header-heading {
    position: relative;
    width: 80%; /* Adjust width as needed */
    max-width: 900px; /* Set maximum width to prevent overflow */
    margin: 0 auto; /* Center the container horizontally */
    padding: 20px; /* Add padding for spacing */
    text-align: center; /* Center-align the text */
    color: #ffffff; /* Set text color to white */
    display: flex; /* Use flexbox for centering */
    flex-direction: column; /* Stack items vertically */
    justify-content: center; /* Center items vertically */
    align-items: center; /* Center items horizontally */
  }
  
  .header-heading h2 {
    font-size: 2rem; /* Adjust font size as needed */
    margin-bottom: 50px;
    margin-top: 100px; /* Add space between elements */
  }
  
  .header-heading h1 {
    font-size: 3.5rem; /* Adjust font size as needed */
    margin-bottom: 20px; /* Add space between elements */
  }
  
  .header-heading .details {
    font-size: 1.2rem; /* Adjust font size as needed */
    margin-bottom: 20px; /* Add space between elements */
    color: #ffffff; /* Set text color to white */
  }
  
  .header-btns {
    display: flex; /* Use flexbox for centering */
    justify-content: center; /* Center items horizontally */
    align-items: center; /* Center items vertically */
    margin: 0px 0px 0px 0px; /* Add margin between text and button */
  }
  
  .header-btn {
    padding: 10px 20px; /* Adjust padding as needed */
    font-size: 1.4rem;
    background-color: #FF1414;
    box-shadow: 5px 10px 30px rgba(255, 2, 2, 0.336);
    border-radius: 5px;
    color: #ffffff;
    text-decoration: none; /* Remove underline from link */
    border: none; /* Remove border */
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s; /* Smooth transition for hover effect */
    margin-left: 0%;
    margin-right: 0%;
    padding: 5px 5px 5px 5px;
    align-items: center;
    align-self: center;
    align-content: center;
    margin: 0px 0px 0px 0px;
  }
  
  .header-btn:hover {
    background-color: transparent;
    color: #ffffff;
    border: 2px solid #FF1414; /* Add border on hover */
  }
  

span {
    color: #fff01e;
}






/*                                                                     Features                             */

#features {
  width: 100%;
  padding: 80px 0; /* Add padding for spacing */
  background-color: #e6eff3;
  text-align: center; /* Center-align the content */
}

#features h1 {
  color: #000000; /* Set text color */
  font-size: 3rem; /* Adjust font size as needed */
  margin-bottom: 40px; /* Add space between heading and content */
}

.a-container {
  display: flex;
  flex-wrap: wrap; /* Allow flex items to wrap */
  justify-content: center; /* Center items horizontally */
  gap: 40px; /* Add gap between feature boxes */
}

.a-box {
  position: relative;
  width: 100%;
  max-width: 300px;
  overflow: hidden;
  text-align: center;
  margin: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}

.a-box:hover {
  transform: scale(1.05);
}

.a-b-img img {
  width: 100%;
  height: auto;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.a-b-text {
  padding: 20px;
}

.a-b-text h2 {
  font-size: 1.5em;
  margin-bottom: 10px;
}

.a-b-text .hidden-text {
  display: none;
  font-size: 1em;
  color: #333;
  transition: opacity 0.3s;
}

.a-box:hover .hidden-text,
.a-box:focus .hidden-text,
.a-box:active .hidden-text {
  display: block;
  opacity: 1;
}

.a-box:hover .a-b-text h2,
.a-box:focus .a-b-text h2,
.a-box:active .a-b-text h2 {
  margin-bottom: 20px;
}



