/*                                                          About styles                                   */


#about {
  padding: 50px 20px;
  background-color: #f4f4f4;
}

.about-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
}

.about-image {
  flex: 1;
  text-align: center;
 
}

.about-image img {
  max-width: 100%;
  height: 100%;
  border-radius: 10px;
}

.about-text {
  flex: 2;
  min-width: 300px;
}

.about-text h1 {
  font-size: 2.5em;
  margin-bottom: 20px;
}

.about-text p {
  font-size: 1.2em;
  margin-bottom: 20px;
}

.about-text button {
  padding: 10px 20px;
  font-size: 1em;
  background-color: #ff6347;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.about-text button:hover {
  background-color: #ff4500;
}

@media (max-width: 768px) {
  .about-container {
    flex-direction: column;
  }

  .about-image {
    order: -1;
  }

  .about-image img {
    max-width: 50%;
    margin-bottom: 20px;
  }

  .about-text h1 {
    font-size: 2em;
  }

  .about-text p {
    font-size: 1em;
  }
}



/*                                                 Contact                                       */


.contact-header {
  color: black;
}


#contact{
    width:100%;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
#contact h1{
    color: #000000;
    font-size: 3rem;
    align-content: center;
    text-align: center;
}
#contact form{
    width:600px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
}
#contact form input,
#contact form textarea{
    width:100%;
    height:50px;
    margin:5px 0px;
    padding: 10px;
    border: 100px;
    border-color: #da0202;
    outline: solid;
    background-color: #ffffff2d;
    color: #030303;
    border-radius: 5px;
}
#contact form textarea{
    height: 150px;
}
#contact form input[type="submit"]{
    height: 45px;
    background: linear-gradient(90deg, #FF1414 20%,#f70000);
    color: #ebb3b3;
    text-transform: uppercase;
}

@media(max-width:620px){
  #contact form{
      width:90%;
  }
}


/*                                                  Footer                                                             */

.footer {
    background-color: #000;
    color: #fff;
    padding: 20px 0;
    text-align: center;
  }
  
  .footer-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: 0%;
  }
  
  .social-media, .contact-info {
    display: flex;
    flex-direction: row ;
    align-items: center;
    margin: 0px 0px;
    padding: 20px 20px 5px 20px;
  }
  
  .social-media a, .contact-info a {
    color: #fff;
    text-decoration: none;
    margin: 5px 0;
    font-size: 1.2em;
    transition: color 0.3s;
    margin: 20px 20px 5px 20px;
    
  }
  
  .social-media a:hover, .contact-info a:hover {
    color: #ff6347; /* Tomato color on hover */
  }
  
  .social-media i, .contact-info i {
    margin-right: 8px;
  }
  
  .contact-info p {
    margin: 20px 20px 5px 20px ;
    font-size: 1.1em;
  }
  
  @media (max-width: 600px) {
    .footer-container {
      flex-direction: column;
    }
  
    .social-media, .contact-info {
      margin: 10px 0;
    }
  }
  





